.machine-content {
  /*table*/
  /*myTree*/
  /*#control_btn{
    display: none;
  }*/
  /* default theme */
}
.machine-content .add_back0 {
    background: #fff !important;
}
.machine-content .add_back1 {
    background: #dee2ed !important;
}
.machine-content .contentArea {
    position: relative;
    padding: 20px 32px 0 32px;
    overflow: hidden;
}
.machine-content .myTable {
    width: 100%;
    height: calc(100% - 20px);
    background: #dee2ed;
}
.machine-content .handsontable .red-color {
    border-color: #355fc3 !important;
}
.machine-content .noData {
    width: 100%;
    position: absolute;
    top: 50%;
    left: 0;
    font-size: 18px;
    text-align: center;
}
.machine-content .create-btn {
    position: absolute;
    top: 30px;
    right: 300px;
    z-index: 999;
    font-size: 16px;
    color: #000;
    width: 112px;
    height: 32px;
    line-height: 32px;
    text-align: center;
    border: 1px solid #fff;
    border-radius: 3px;
    background: #fff;
}
.machine-content .toggle-btn, .machine-content .keep-btn {
    position: absolute;
    top: 30px;
    right: 60px;
    font-size: 16px;
    color: #fff;
    width: 112px;
    height: 32px;
    line-height: 32px;
    text-align: center;
    border: 1px solid #fff;
    border-radius: 3px;
    background: transparent;
}
.machine-content .toggle-btn:hover, .machine-content .keep-btn:hover {
      background: #fff;
      color: #000;
}
.machine-content .keep-btn {
    right: 180px;
}
.machine-content #tableContent {
    height: calc(100% - 40px);
}
.machine-content .handsontable tr {
    background: #888;
}
.machine-content .handsontable th {
    background-color: #fff;
    color: #000;
    line-height: 40px;
    font-size: 16px;
}
.machine-content .htCore thead th {
    line-height: 40px;
}
.machine-content .handsontable thead th .relative {
    padding: 0 4px;
}
.machine-content .htCore tbody th {
    line-height: 31px;
}
.machine-content .handsontable td {
    border-color: #888;
    line-height: 30px;
    text-align: center;
    font-size: 14px;
}
.machine-content .handsontable .changeType {
    background: transparent;
    border-radius: 0;
    border: none;
    color: #bbb;
    font-size: 9px;
    line-height: 9px;
    padding: 2px;
    margin: 12px 1px 0 5px;
    float: right;
}
.machine-content .handsontable .changeType:hover {
    border: none;
}
.machine-content .handsontable .columnSorting.sortAction:hover {
    text-decoration: none;
    cursor: pointer;
}
.machine-content .handsontable thead th {
    border-color: #888;
}
.machine-content .handsontable tr:first-child td, .machine-content .handsontable tr:first-child th {
    border-top: 1px solid #888;
}
.machine-content .handsontable th:last-child {
    border-right: 1px solid #888;
    border-bottom: 1px solid #888;
}
.machine-content .handsontable th:first-child {
    border-left: 1px solid #888;
}
.machine-content .handsontable .htNoFrame + td, .machine-content .handsontable .htNoFrame + th, .machine-content .handsontable.htRowHeaders thead tr th:nth-child(2), .machine-content .handsontable td:first-of-type, .machine-content .handsontable th:first-child, .machine-content .handsontable th:nth-child(2) {
    border-left: 1px solid #888;
}
.machine-content .handsontable.listbox tr:first-child td {
    border-color: #888;
    border-top-color: transparent;
}
.machine-content .handsontable.listbox td {
    border-color: #888;
}
.machine-content .controls-btn {
    height: 58px;
    background: #fff;
    padding-left: 0;
    border: 1px solid #888;
    border-radius: 7px;
    overflow: hidden;
    position: relative;
    display: flex;
    z-index: 2;
}
.machine-content .push-controls {
    width: 60px;
    height: 58px;
    line-height: 62px;
    text-align: center;
    border-right: 1px solid #436EF6;
    background: #fff;
}
.machine-content .push-right {
    border-left: 1px solid #436EF6;
    border-right: none;
    position: absolute;
    top: 0;
    right: 0;
}
.machine-content .btn-content {
    position: absolute;
    top: 0;
    left: 60px;
    height: 58px;
    display: inline-block;
    z-index: -1;
    cursor: move;
}
.machine-content .choose-btn {
    height: 58px;
    padding-left: 0px;
    border-radius: 7px;
    position: relative;
}
.machine-content .choose-btn .choose-content {
      padding-left: 10px;
      position: absolute;
      height: 420px;
      width: 150px;
      top: -477px;
      left: 3px;
      overflow-x: hidden;
      overflow-y: auto;
      border: 2px solid #436EF6;
      color: #436EF6;
      border-radius: 10px;
      background: #fff;
      z-index: 999;
}
.machine-content .choose-btn .choose-content .choose-item {
        height: 30px;
        font-size: 16px;
        text-align: left;
}
.machine-content .choose-btn .choose-content .choose-item:hover label {
          font-weight: bold;
          cursor: pointer;
}
.machine-content .choose-btn .choose-content .choose-item .choose-name {
          display: inline-block;
          line-height: 30px;
          display: inline-block;
          height: 30px;
          margin-right: 5px;
}
.machine-content .choose-btn .choose-content .choose-item .check-box {
          position: relative;
          display: inline-block;
          height: 14px;
          width: 14px;
}
.machine-content .choose-btn .choose-content .choose-item .check-box input[type="checkbox"] {
            position: absolute;
            left: 0;
            top: 0;
            visibility: hidden;
}
.machine-content .choose-btn .choose-content .choose-item .check-box input[type="checkbox"]:checked + label {
            border-color: green;
            border: 2px solid #436EF6;
}
.machine-content .choose-btn .choose-content .choose-item .check-box input[type="checkbox"]:checked + label::after {
              height: 7px;
              transition: height .2s linear;
}
.machine-content .choose-btn .choose-content .choose-item .check-box input[type="checkbox"]:checked + label::before {
              height: 16px;
              transition: height .1s linear .2s;
}
.machine-content .choose-btn .choose-content .choose-item .check-box .choose-box {
            position: absolute;
            left: 0;
            top: 0;
            height: 12px;
            width: 12px;
            border: 2px solid #9ab8f5;
            border-radius: 5px;
}
.machine-content .choose-btn .choose-content .choose-item .check-box .choose-box::before, .machine-content .choose-btn .choose-content .choose-item .check-box .choose-box::after {
              content: "";
              position: absolute;
              display: block;
              width: 2px;
              transform-origin: left top;
              border-radius: 2px;
              background-color: #436EF6;
}
.machine-content .choose-btn .choose-content .choose-item .check-box .choose-box::after {
              content: "";
              left: 0px;
              top: 5px;
              height: 0;
              transform: rotate(-42deg);
}
.machine-content .choose-btn .choose-content .choose-item .check-box .choose-box::before {
              content: "";
              left: 6px;
              top: 12px;
              height: 0;
              transform: rotate(-144deg);
              border: 1px solid #fff;
}
.machine-content .myTree {
    width: 100%;
    height: calc(100% - 50px);
    overflow: hidden;
}
.machine-content #jsmind_container {
    width: 100%;
    height: 100%;
    background: #DEE2ED;
}
.machine-content .jsmind-inner::-webkit-scrollbar {
    width: 10px;
    height: 10px;
}
.machine-content .jsmind-inner::-webkit-scrollbar-thumb {
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
    background: #535353;
}
.machine-content .jsmind-inner::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
    border-radius: 10px;
    background: #EDEDED;
}
.machine-content .right-click {
    width: 150px;
    position: absolute;
    z-index: 9;
    background: #fff;
    border: 1px solid #ccc;
}
.machine-content .right-click a {
      display: block;
      height: 34px;
      font-size: 15px;
      line-height: 34px;
      text-align: center;
      color: #798CA1;
}
.machine-content .right-click a:hover {
        background: #436EF6;
        color: #fff;
}
.machine-content jmnode:hover, .machine-content button:hover {
    cursor: pointer;
}
.machine-content .up {
    text-align: center;
}
.machine-content .up .up_angle {
      display: inline-block;
      width: 0;
      height: 0;
      border-right: 10px solid transparent;
      border-left: 10px solid transparent;
      border-bottom: 10px solid #436EF6;
}
.machine-content .up .up_angle:hover {
        cursor: pointer;
        border-bottom-color: #1248f4;
}
.machine-content .down {
    text-align: center;
}
.machine-content .down .down_angle {
      display: inline-block;
      width: 0;
      height: 0;
      border-right: 10px solid transparent;
      border-left: 10px solid transparent;
      border-top: 10px solid #436EF6;
}
.machine-content .down .down_angle:hover {
        cursor: pointer;
        border-top-color: #1248f4;
}
.machine-content .right .right_angle {
    display: inline-block;
    width: 0;
    height: 0;
    border-top: 10px solid transparent;
    border-bottom: 10px solid transparent;
    border-left: 10px solid #436EF6;
}
.machine-content .right .right_angle:hover {
      cursor: pointer;
      border-left-color: #1248f4;
}
.machine-content .btn-reset {
    position: absolute;
    top: 60px;
    right: 15px;
    padding: 6px 25px;
    background-color: #fff;
    color: #798ca1;
    border-radius: 4px;
    border: none;
    box-shadow: 1px 1px 1px #666;
    font: 16px MicrosoftYaHei;
    z-index: 3;
}
.machine-content .btn-reset:hover {
      box-shadow: 2px 2px 8px #000;
      background-color: #ebebeb;
      color: #333;
}
.machine-content .edit-station {
    position: absolute;
    top: 60px;
    left: 0;
    width: 310px;
    background: #fff;
    z-index: 10;
}
.machine-content .edit-station .title {
      background: #343c45;
      padding: 6px 15px;
      font-size: 18px;
      color: #fff;
      border-top-left-radius: 4px;
      border-top-right-radius: 4px;
}
.machine-content .edit-station .edit-content {
      padding: 15px;
}
.machine-content .edit-station .edit-block {
      font-size: 14px;
      margin-bottom: 5px;
      position: relative;
}
.machine-content .edit-station .edit-input, .machine-content .edit-station .edit-select {
      width: 110px;
      padding: 4px 12px;
      border: 1px solid #ccc;
}
.machine-content .edit-station .edit-input:focus, .machine-content .edit-station .edit-select:focus {
        border-color: #436EF6;
}
.machine-content .edit-station .edit-select {
      width: 136px;
}
.machine-content .edit-station .addDevice {
      height: 28px;
      line-height: 28px;
      background: #436EF6;
      border: 1px solid #436EF6;
      color: #fff;
      padding: 0 6px;
      border-radius: 4px;
}
.machine-content .edit-station .delBtn {
      height: 25px;
      line-height: 25px;
      font-size: 12px;
      padding: 0 2px;
      position: absolute;
      right: 0;
      top: 0;
}
.machine-content .edit-station .edit-label {
      display: inline-block;
      width: 70px;
}
.machine-content .edit-station .device {
      padding: 5px 0;
      border-bottom: 1px dashed #333;
}
.machine-content .edit-station .device-list {
      max-height: calc(100vh - 330px);
      overflow-y: auto;
}
.machine-content .edit-station .device-list::-webkit-scrollbar {
        width: 6px;
        height: 6px;
}
.machine-content .edit-station .device-list::-webkit-scrollbar-thumb {
        border-radius: 6px;
        -webkit-box-shadow: inset 0 0 3px rgba(0, 0, 0, 0.2);
        background: #535353;
}
.machine-content .edit-station .device-list::-webkit-scrollbar-track {
        -webkit-box-shadow: inset 0 0 3px rgba(0, 0, 0, 0.2);
        border-radius: 6px;
        background: #EDEDED;
}
.machine-content jmnode {
    padding: 15px 20px;
    background-color: #fff;
    color: #798CA1;
    border-radius: 4px;
    box-shadow: 1px 1px 1px #666;
    font: 16px MicrosoftYaHei;
}
.machine-content jmnode:hover {
    box-shadow: 2px 2px 8px #000;
    background-color: #ebebeb;
    color: #333;
}
.machine-content jmnode.selected {
    background-color: #fff;
    color: #436EF6;
    box-shadow: 2px 2px 8px #436EF6;
}
.machine-content jmnode.root {
    font-size: 16px;
}
.machine-content jmexpander {
    border-color: #436EF6;
}
.machine-content jmexpander:hover {
    border-color: #436EF6;
}
.machine-content jmexpander {
    line-height: 9px;
    font-size: 16px;
    color: #436EF6;
}
.machine-content {
  position: relative;
}
.add {
  display: none;
}
